html {
  background-color: #f1f1f1;
  margin: 0 15px;
}

body {
  font-family: SegoeUI, "Helvetica Neue", Helvetica, Arial, sans-serif;
  max-width: 1000px;
  margin: 0 auto;
}

* {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

img {
  max-width: 100%;
}

.touchable {
  position: relative;
  overflow: hidden;
}

.touchable:before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  content: " ";
  background-color: black;
  -webkit-transition-duration: 150ms;
       -o-transition-duration: 150ms;
          transition-duration: 150ms;
}

.touchable:hover:before {
  opacity: 0.05;
}

img.menu {
  margin-top: -4px;
  float: left;
  width: 28px;
  cursor: pointer;
}

section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  min-height: 500px;
  -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.12);
          box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.12);
  border-radius: 3px;
  overflow: hidden;
  background-color: white;
  -webkit-animation-name: TransitionUp;
          animation-name: TransitionUp;
  -webkit-animation-duration: 500ms;
          animation-duration: 500ms;
  margin-bottom: 85px;
}

section > div:first-of-type {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 0.5 1 0%;
      flex: 0.5 1 0%;
  background-size: cover;
  background-position: center center;
}

section > article:first-of-type {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 0.5 1 0%;
      flex: 0.5 1 0%;
}

section > article:first-of-type > div:first-of-type {
  padding: 25px;
  white-space: pre-line;
  font-size: 18px;
  line-height: 1.4;
}

@-webkit-keyframes TransitionUp {
  from {
    -webkit-transform: translateY(25px);
            transform: translateY(25px);
    opacity: 0;
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@keyframes TransitionUp {
  from {
    -webkit-transform: translateY(25px);
            transform: translateY(25px);
    opacity: 0;
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@-webkit-keyframes TransitionUpLarge {
  from {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
    opacity: 0;
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@keyframes TransitionUpLarge {
  from {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
    opacity: 0;
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

nav {
  z-index: 9;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-animation-name: NavigationTransition;
          animation-name: NavigationTransition;
  -webkit-animation-duration: 400ms;
          animation-duration: 400ms;
  background-color: rgba(255, 255, 255, 0.75);
}

nav.hide {
  -webkit-animation-name: NavigationTransitionOut;
          animation-name: NavigationTransitionOut;
}

nav > div:first-of-type {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.12);
          box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.12);
  border-radius: 3px;
  overflow: hidden;
  background-color: white;
}

@-webkit-keyframes NavigationTransition {
  from {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    opacity: 0;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@keyframes NavigationTransition {
  from {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    opacity: 0;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@-webkit-keyframes NavigationTransitionOut {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
  to {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    opacity: 0;
  }
}

@keyframes NavigationTransitionOut {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
  to {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    opacity: 0;
  }
}

h1 {
  margin-bottom: 25px;
}

.list-item-container {
  clear: both;
  margin: 0 -15px -30px;
}

.list-item {
  cursor: pointer;
  margin: 0 15px 30px;
  float: left;
  width: calc(33.3% - 30px);
  -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.12);
          box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.12);
  border-radius: 3px;
  overflow: hidden;
  background-color: white;
  -webkit-animation-name: TransitionUpLarge;
          animation-name: TransitionUpLarge;
  -webkit-animation-duration: 500ms;
          animation-duration: 500ms;
}

.list-item > div:first-of-type {
  height: 200px;
  background-size: cover;
  background-position: center center;
}

.list-item > h4:first-of-type {
  padding: 25px;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  color: black;
}

footer {
  clear: both;
  width: 100%;
  -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.12);
          box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.12);
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  overflow: hidden;
  background-color: white;
  height: 25px;
  margin-top: 15px;
}

footer:after {
  font-size: 12px;
  content: "Copyright (c) 2017-present Evan Jones";
  line-height: 25px;
  padding: 0 15px;
}

footer:before {
  font-size: 15px;
  line-height: 25px;
  content: "ðŸ˜ƒ";
  float: right;
  padding: 0 15px;
}

.container {
  min-height: 100vh;
}

.hover {
  background-color: rgba(0, 0, 0, 0);
  -webkit-transition-duration: 150ms;
       -o-transition-duration: 150ms;
          transition-duration: 150ms;
}
.hover:hover {
  background-color: rgba(0, 0, 0, 0.05);
}



